import React, { useState, useEffect } from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import {
  full,
  fullAbsolute,
  imageSlide,
} from '../styles'

export default function Slides(props) {
  const {
    alt = 'background',
    imageStyle,
    useRef,
    slides,
    style,
    delay = 5000,
    ...rest
  } = props

  const [zoomed, setZoomed] = useState(false)

  function toggle() {
    const { current = { swiper: { zoom: { out: console.error } } } } = useRef
    if (current.swiper.zoom.scale === 1/*  && !zoomed */) {
      setZoomed(true)
      current.swiper.zoom.in()
      current.swiper.autoplay.stop()
      console.log(current.swiper.autoplay)
    } else if (current.swiper.zoom.scale > 1 /* && zoomed */) {
      setZoomed(false)
      current.swiper.zoom.out()
      current.swiper.autoplay.start()
    }

  }

  return (
    <Swiper
      ref={useRef}
      threshold={5}
      spaceBetween={0}
      slidesPerView={1}
      direction="horizontal"
      effect="fade"
      //loop
      speed={2500}
      autoplay={{ delay }}
      style={{
        ...full,
        ...fullAbsolute,
        ...(
          rest.zoom ? { overflow: 'visible' } : {}
        ),
        ...style
      }}
      {...rest}
    >
      {
        slides.map((slide, index) => (
          <SwiperSlide key={`${alt}_slide_${index}`}>
            <div className="swiper-zoom-container" onDoubleClick={toggle}>
              <img alt={`${alt}_slide_${index}`} style={{ ...imageSlide, ...imageStyle }} src={slide} />
            </div>
          </SwiperSlide>
        ))
      }
    </Swiper>
  )
}
