import React from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import { BuildSection } from '../components'

import {
  full,
} from '../styles'

import { backgrounds } from '../assets'

import Builder from './Builder';

const { bg1, bg2, bg3 } = backgrounds

export default function BuildSite(props) {
  const { structure } = props
  return (
    <div style={full}>
      <Builder.Slides
        delay={10000}
        alt="site_background"
        slides={[bg1, bg2, bg3]}
      />
      <Swiper
        spaceBetween={0}
        slidesPerView={1}
        threshold={20}
        //nested
        /* hashNavigation={{
          watchState: true
        }} */
        direction="vertical"
        //loop
        style={full}
        scrollbar={{ draggable: true }}
      >{
          structure.map((section, index) => (
            <SwiperSlide key={`section${index}`}>
              <BuildSection scenes={section} />
            </SwiperSlide>
          ))
        }</Swiper>
    </div>
  )
}